import { SPHttpClient, SPHttpClientResponse } from '@microsoft/sp-http';
import {ListItemFactory} from './ListItemFactory';
import {IAnnouncementListItem} from './models/IAnnouncementListItem';

export class AnnouncementListItemFactory extends ListItemFactory {
    public _getItems(requester: SPHttpClient, siteUrl: string, listName: string): IAnnouncementListItem[] {
        let items: IAnnouncementListItem[];
        requester.get(`${siteUrl}/_api/web/lists/getbytitle('${listName}')/items?$select=Title,Id`,
        SPHttpClient.configurations.v1,
        {
            headers: {
            'Accept': 'application/json;odata=nometadata',
            'odata-version': ''
            }
        })
        .then((response: SPHttpClientResponse): Promise<{ value: IAnnouncementListItem[] }> => {
            return response.json();
        })
        .then((response: { value: IAnnouncementListItem[] }): void => {
            items= response.value;
        });
        return items;
    }
}
